}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body, html {
  font-family: Helvetica;
}


@media screen and (max-width: 600px) {
  .content-container{ 
    width: 95vw;
    height: 90vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .greeting-header {
    font-weight: bold;
    font-size: 30px;
  }
  
  .greeting-text {
    width: 350px;
    font-size: 15px;
  }
  
  .about-points {
    font-size: 15px;
    list-style-type: none;
  }
  
  .links-container {
    margin-top: 30px;
    display: flex; 
    justify-content: center;
  }
  
  .links {
    color: #828282;
    width: 70%;
    display: flex;
    flex-direction: row;
    font-size: 12px;
    justify-content: space-evenly;
  }
  
  a.link-text {
    color: #828282;
    text-decoration: none;
  }
  
  a.link-text:hover {
    color: black;
  }
  
  a.about-links {
    color: black;
    text-decoration: none;
  }
  
  .school{
    font-weight: bold;
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(59, 125, 161, 0.40)), to(rgba(59, 125, 161, 0.40)));
    background-image: linear-gradient(rgba(59, 125, 161, 0.40), rgba(59, 125, 161, 0.40));
    background-repeat: no-repeat;
    background-position: 0% 100%;
    background-size: 100% 2.5px;
    -webkit-transition: background-size .2s ease-in .1s;
    transition: background-size .2s ease-in .1s;
  }
  
  .school:hover {
    -webkit-transition: background-size .2s ease-in .1s;
    transition: background-size .2s ease-in .1s;
    background-size: 100% 100%;
    background-position: 0% 100%;
  }
  
  .intern {
    font-weight: bold; 
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 153, 0, 0.60)), to(rgba(255, 153, 0, 0.60))); 
    background-image: linear-gradient(rgba(255, 153, 0, 0.60), rgba(255, 153, 0, 0.60));
    background-repeat: no-repeat;
    background-position: 0% 100%;
    background-size: 100% 2.5px;
    -webkit-transition: background-size .2s ease-in .1s;
    transition: background-size .2s ease-in .1s;
  }
  
  .intern:hover {
    -webkit-transition: background-size .2s ease-in .1s;
    transition: background-size .2s ease-in .1s;
    background-size: 100% 100%;
    background-position: 0% 100%;
  }
  
  .codebase {
    font-weight: bold; 
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(47, 59, 121, 0.30)), to(rgba(47, 59, 121, 0.30))); 
    background-image: linear-gradient(rgba(47, 59, 121, 0.30), rgba(47, 59, 121, 0.30));
    background-repeat: no-repeat;
    background-position: 0% 100%;
    background-size: 100% 2.5px;
    -webkit-transition: background-size .2s ease-in .1s;
    transition: background-size .2s ease-in .1s;
  }
  
  .codebase:hover {
    -webkit-transition: background-size .2s ease-in .1s;
    transition: background-size .2s ease-in .1s;
    background-size: 100% 100%;
    background-position: 0% 100%;
  }
  
  .uma {
    font-weight: bold; 
    background-image: -webkit-gradient(linear,left top, left bottom,from(rgba(255, 102, 51, 0.40)),to(rgba(255, 102, 51, 0.40))); 
    background-image: linear-gradient(rgba(255, 102, 51, 0.40),rgba(255, 102, 51, 0.40));
    background-repeat: no-repeat;
    background-position: 0% 100%;
    background-size: 100% 2.5px;
    -webkit-transition: background-size .2s ease-in .1s;
    transition: background-size .2s ease-in .1s;
  }
  
  .uma:hover {
    -webkit-transition: background-size .2s ease-in .1s;
    transition: background-size .2s ease-in .1s;
    background-size: 100% 100%;
    background-position: 0% 100%;
  }
  
  ul {
    line-height: 150%;
  }
  
  .wave:hover {
    -webkit-animation-name: wave-animation;
            animation-name: wave-animation; 
    -webkit-animation-duration: 1.2s; 
            animation-duration: 1.2s;       
    -webkit-animation-iteration-count: infinite;       
            animation-iteration-count: infinite; 
    -webkit-transform-origin: 70% 70%; 
            transform-origin: 70% 70%;      
    display: inline-block;
  }
  
  @-webkit-keyframes wave-animation {
    0% { -webkit-transform: rotate( 0.0deg); transform: rotate( 0.0deg) }
   10% { -webkit-transform: rotate(14.0deg); transform: rotate(14.0deg) }  
   20% { -webkit-transform: rotate(-8.0deg); transform: rotate(-8.0deg) }
   30% { -webkit-transform: rotate(14.0deg); transform: rotate(14.0deg) }
   40% { -webkit-transform: rotate(-4.0deg); transform: rotate(-4.0deg) }
   50% { -webkit-transform: rotate(10.0deg); transform: rotate(10.0deg) }
   60% { -webkit-transform: rotate( 0.0deg); transform: rotate( 0.0deg) } 
  100% { -webkit-transform: rotate( 0.0deg); transform: rotate( 0.0deg) }
  }
  
  @keyframes wave-animation {
    0% { -webkit-transform: rotate( 0.0deg); transform: rotate( 0.0deg) }
   10% { -webkit-transform: rotate(14.0deg); transform: rotate(14.0deg) }  
   20% { -webkit-transform: rotate(-8.0deg); transform: rotate(-8.0deg) }
   30% { -webkit-transform: rotate(14.0deg); transform: rotate(14.0deg) }
   40% { -webkit-transform: rotate(-4.0deg); transform: rotate(-4.0deg) }
   50% { -webkit-transform: rotate(10.0deg); transform: rotate(10.0deg) }
   60% { -webkit-transform: rotate( 0.0deg); transform: rotate( 0.0deg) } 
  100% { -webkit-transform: rotate( 0.0deg); transform: rotate( 0.0deg) }
  }
}

