@import url('https://fonts.googleapis.com/css?family=Heebo:regular,bold,light&subset=latin,latin-ext');


body, html {
  font-family: Helvetica;
}


@media screen and (max-width: 600px) {
  .content-container{ 
    width: 95vw;
    height: 90vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .greeting-header {
    font-weight: bold;
    font-size: 30px;
  }
  
  .greeting-text {
    width: 350px;
    font-size: 15px;
  }
  
  .about-points {
    font-size: 15px;
    list-style-type: none;
  }
  
  .links-container {
    margin-top: 30px;
    display: flex; 
    justify-content: center;
  }
  
  .links {
    color: #828282;
    width: 70%;
    display: flex;
    flex-direction: row;
    font-size: 12px;
    justify-content: space-evenly;
  }
  
  a.link-text {
    color: #828282;
    text-decoration: none;
  }
  
  a.link-text:hover {
    color: black;
  }
  
  a.about-links {
    color: black;
    text-decoration: none;
  }
  
  .school{
    font-weight: bold;
    background-image: linear-gradient(rgba(59, 125, 161, 0.40), rgba(59, 125, 161, 0.40));
    background-repeat: no-repeat;
    background-position: 0% 100%;
    background-size: 100% 2.5px;
    transition: background-size .2s ease-in .1s;
  }
  
  .school:hover {
    transition: background-size .2s ease-in .1s;
    background-size: 100% 100%;
    background-position: 0% 100%;
  }
  
  .intern {
    font-weight: bold; 
    background-image: linear-gradient(rgba(255, 153, 0, 0.60), rgba(255, 153, 0, 0.60));
    background-repeat: no-repeat;
    background-position: 0% 100%;
    background-size: 100% 2.5px;
    transition: background-size .2s ease-in .1s;
  }
  
  .intern:hover {
    transition: background-size .2s ease-in .1s;
    background-size: 100% 100%;
    background-position: 0% 100%;
  }
  
  .codebase {
    font-weight: bold; 
    background-image: linear-gradient(rgba(47, 59, 121, 0.30), rgba(47, 59, 121, 0.30));
    background-repeat: no-repeat;
    background-position: 0% 100%;
    background-size: 100% 2.5px;
    transition: background-size .2s ease-in .1s;
  }
  
  .codebase:hover {
    transition: background-size .2s ease-in .1s;
    background-size: 100% 100%;
    background-position: 0% 100%;
  }
  
  .uma {
    font-weight: bold; 
    background-image: linear-gradient(rgba(255, 102, 51, 0.40),rgba(255, 102, 51, 0.40));
    background-repeat: no-repeat;
    background-position: 0% 100%;
    background-size: 100% 2.5px;
    transition: background-size .2s ease-in .1s;
  }
  
  .uma:hover {
    transition: background-size .2s ease-in .1s;
    background-size: 100% 100%;
    background-position: 0% 100%;
  }
  
  ul {
    line-height: 150%;
  }
  
  .wave:hover {
    animation-name: wave-animation; 
    animation-duration: 1.2s;       
    animation-iteration-count: infinite; 
    transform-origin: 70% 70%;      
    display: inline-block;
  }
  
  @keyframes wave-animation {
    0% { transform: rotate( 0.0deg) }
   10% { transform: rotate(14.0deg) }  
   20% { transform: rotate(-8.0deg) }
   30% { transform: rotate(14.0deg) }
   40% { transform: rotate(-4.0deg) }
   50% { transform: rotate(10.0deg) }
   60% { transform: rotate( 0.0deg) } 
  100% { transform: rotate( 0.0deg) }
  }
}

